___CSS_LOADER_EXPORT___.push([module.id, `.setup-menu {
  padding: 20px;
}
.select {
  display: flex;
  width: 30%;
  min-width: 100px;
  flex-direction: column;
  gap: 10px;
}

.select__level {
  overflow: hidden;
  position: relative;
  display: flex;
  border: 1px solid black;
  gap: 10px;
  background: beige;
  padding: 5px;
  border-radius: 20px;
}

.level__title {
  transition: all 0.5 ease;
}

.level__game {
  position: absolute;
  transition: all 0.5s ease;
  font-size: 0;
  display: flex;
  flex-direction: column;
  gap: 3px;
  font-size: 12px;
  left: 105px;
}

.select__game {
  border-bottom: 1px solid black;
  cursor: pointer;
}
.select__game:nth-child(5n) {
  border-bottom: none;
}
.select__game_value:nth-child() {
  border-top: 1px solid black;
  border-bottom: 1px solid black;
}
.level__btn {
  cursor: pointer;
  position: relative;
  width: 20px;
  height: 20px;
  background: black;
  border-radius: 30px;
}
.--active {
  .level__title {
    display: none;
  }
  .level__game {
    position: static;
  }
  .level__btn::after {
    transform: rotate(-45deg);
  }
  .level__btn::before {
    transform: rotate(45deg);
  }
}
.level__btn::after {
  content: "";
  position: absolute;
  width: 1px;
  height: 8px;
  background-color: white;
  top: 5px;
  right: 10px;
  transform: rotate(45deg);
  transition: all 0.4s ease;
}
.level__btn::before {
  content: "";
  position: absolute;
  width: 1px;
  height: 8px;
  background-color: white;
  top: 10px;
  right: 10px;
  transform: rotate(-45deg);
  transition: all 0.4s ease;
}
